import React, {useEffect} from "react";
import {useParams} from "react-router";
import classes from "./showPage.module.css";
import background from "./../../assets/background.jpg";
import poster_placeholder from "./../../assets/no_poster.png";
import {useDispatch, useSelector} from "react-redux";
import {getCurrentShow} from "../../http";
import {Header, Preloader, Description} from "../../components";
import InfoBlock from "../../components/InfoBlock/infoBlock";

export const ShowPage = () => {
  const dispatch = useDispatch();
  const params = useParams();
  const show_id = params.id;

  const currentShow = useSelector((state) => state.shows.currentShow);
  const isFetching = useSelector((state) => state.global.isFetching);

  const seasons = currentShow ? currentShow.seasons : [];
  const lastSeason = seasons.length > 0 ? seasons[seasons.length - 1] : null;

  const trailerItem =
    currentShow && currentShow.videos && currentShow.videos.results
      ? currentShow.videos.results.find((item) => item.type === "Trailer")
      : null;

  useEffect(() => {
    dispatch(getCurrentShow(show_id));
  }, [dispatch, show_id]);

  if (isFetching || !currentShow) {
    return <Preloader />;
  }

  return (
    <>
      <Header />
      <div className={classes.wrapper}>
        <div className={classes.container}>
          <img className={classes.backgroundImage} src={background} alt="background" />
          <div className={classes.content}>
            <img
              className={classes.posterImage}
              alt="Poster"
              src={
                currentShow.poster_path
                  ? `https://image.tmdb.org/t/p/original${currentShow.poster_path}`
                  : poster_placeholder
              }
            />
            <Description data={currentShow} type={"tv"} />
          </div>
        </div>
        <InfoBlock
          currentContent={currentShow}
          contentType="tv"
          isMovie={false}
          lastSeason={lastSeason}
          trailerItem={trailerItem}
        />
      </div>
    </>
  );
};
