import React from "react";
import classes from "./infoBlock.module.css";
import {ReviewCarousel, DetailsBlock, CsCarousel, Trailer, SmCarousel, LastSeason} from "../../components";
import {useLocation} from "react-router";
import ImageGallery from "../gallery/ImageGallery";

const InfoBlock = ({currentContent, contentType, isMovie, lastSeason, trailerItem}) => {
  let location = useLocation();

  return (
    <div className={classes.container}>
      {currentContent.reviews.length === 0 ? null : <ReviewCarousel items={currentContent.reviews} />}

      {!isMovie ? (
        <div className={classes.details}>
          <div>
            <LastSeason season={lastSeason} title={currentContent.name} />

            {!currentContent.images ? null : <ImageGallery currentContent={currentContent} />}
            {trailerItem ? <Trailer id={trailerItem.key} /> : null}

            {currentContent.credits.cast.length > 0 ? (
              <div className={classes.castBlock}>
                <h3 className={classes.castHeader}>The cast of the series</h3>
                <CsCarousel
                  items={currentContent.credits.cast.slice(0, 11)}
                  moreButton={`${location.pathname}/cast_crew`}
                />
              </div>
            ) : null}
          </div>

          <DetailsBlock content={currentContent} variant="tv" content_type="tv" />
        </div>
      ) : (
        <>
          <DetailsBlock content={currentContent} content_type={contentType} />
          {!currentContent.images ? null : <ImageGallery currentContent={currentContent} />}
          {trailerItem ? <Trailer id={trailerItem.key} /> : null}
          {currentContent.credits.cast.length > 0 ? (
            <div className={classes.castBlock}>
              <h3 className={classes.castHeader}>{isMovie ? "The cast of the movie" : "The cast of the series"}</h3>
              <CsCarousel
                items={currentContent.credits.cast.slice(0, 11)}
                moreButton={`${location.pathname}/cast_crew`}
              />
            </div>
          ) : null}

          {currentContent.video_url ? <Trailer id={currentContent.video_url.key} /> : null}
        </>
      )}

      {currentContent.similar.length === 0 ? null : (
        <SmCarousel
          header={isMovie ? "Similar movies" : "Similar shows"}
          isLight={true}
          items={currentContent.similar}
          moreButton={`/movflix/categories/${isMovie ? "movie" : "tv"}/similar/${currentContent.id}`}
          marginTop={"30px"}
        />
      )}
    </div>
  );
};

export default InfoBlock;
