import React, {useMemo} from "react";
import classes from "./ImageGallery.module.css";

const getRandomImages = (imagesData, count) => {
  const posters = imagesData.posters || [];
  const backdrops = imagesData.backdrops || [];

  const labeledImages = [...posters, ...backdrops].map((img) => ({
    ...img,
    aspect_ratio: img.aspect_ratio || 1,
  }));

  const shuffledImages = labeledImages.sort(() => 0.5 - Math.random());
  return shuffledImages.slice(0, count);
};

const ImageGallery = ({currentContent}) => {
  const images = useMemo(() => getRandomImages(currentContent.images, 10), [currentContent.images]);

  // Divide images into 3 columns
  const columnCount = 3;
  const columns = Array.from({length: columnCount}, () => []);

  images.forEach((image, index) => {
    columns[index % columnCount].push(image);
  });

  return (
    <div className={classes.imageBlock}>
      {columns.map((columnImages, colIndex) => (
        <div key={colIndex} className={classes.column}>
          {columnImages.map((image, index) => (
            <div key={index} className={classes.imageItem}>
              <img src={`https://image.tmdb.org/t/p/original${image.file_path}`} alt={`Gallery Image ${index + 1}`} />
            </div>
          ))}
        </div>
      ))}
    </div>
  );
};

export default ImageGallery;
